
import './App.css';
import Footer from './Global Components/Footer';
import Header from './Global Components/Header';
import { BrowserRouter, Route } from 'react-router-dom';
import Navbar from './Global Components/Navbar';
import Home from './Page Components/Home';
import About from './Page Components/About';
import Catalouge from './Page Components/Catalouge';
import Store from './Page Components/Store';
import Address from './Page Components/Address';
import Filter from './Page Components/Filter';
import Authors from './Page Components/Authors';
import Feedback from './Page Components/Feedback';
import Review from './Page Components/Review';
import Wishlist from './Page Components/Wishlist';


function App() {
  return (
    <div className="App">
      <Header />
      <BrowserRouter>
        <Route path="/" component={Navbar} />
        <Route path="/home" component={Home} />
        <Route path="/about" component={About} />
        <Route path="/catalouge" component={Catalouge} />
        <Route path="/search" component={Filter} />
        <Route path="/store" component={Store} />
        <Route path="/store/:city" component={Address} />
        <Route path="/authors" component={Authors} />
        <Route path="/feedback" component={Feedback} />
        <Route path="/review" component={Review} />
        <Route path="/wishlist" component={Wishlist} />
        
      </BrowserRouter>
      
      <Footer/>
    </div>
  );
}

export default App;
